:root {
  --c-bg: #000;
  --c-text: #f2f2f2;
  --fs-text: 14px;
  --fs-heading: 18px;
  --fs-title: 60px;
  --column: 90px;
  --margin: 80px;
}

*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  min-height: 100vh;
  width: 100vw;
  color: var(--c-text);
  font-family: Cinzel, serif;
  font-weight: 400;
  font-size: var(--fs-text);
  font-feature-settings: "liga";
  text-size-adjust: none;
  -webkit-font-smoothing: antialiased;
  background: url("bg-projects.fd533cd0.png") center / cover no-repeat fixed;
  display: block;
}

div {
  display: block;
}

.pageHeader {
  z-index: 2;
  width: calc(var(--column) * 9);
  height: calc(var(--fs-title)  + .8rem);
  padding-top: 50px;
  padding-right: var(--margin);
  box-sizing: content-box;
  border-bottom: 1px solid;
  justify-content: space-between;
  align-items: baseline;
  column-gap: 1rem;
  display: flex;
  position: absolute;
  top: 0;
  right: 0;
}

.pageHeader .title {
  align-items: baseline;
  column-gap: 1rem;
  display: flex;
}

.pageHeader .title .label {
  font-size: var(--fs-heading);
}

.pageHeader .title .name {
  font-size: var(--fs-title);
}

.siteHeader {
  z-index: 2;
  position: fixed;
  top: 40px;
  left: 40px;
}

.siteHeader_title {
  letter-spacing: -.02em;
  cursor: crosshair;
  font-family: Cinzel, serif;
  font-size: 30px;
  line-height: .9;
}

.siteHeader_title a {
  color: var(--c-text);
  cursor: crosshair;
  text-decoration: none;
}

.siteHeader_title .line3 {
  text-align: right;
}

.info_inner {
  z-index: 1;
  position: absolute;
  top: 13rem;
  right: 17rem;
}

.infoItem {
  padding: 0 0 6rem;
  position: relative;
}

h2 {
  font-family: Cinzel, serif;
  font-size: 19px;
}

.four h2 {
  font-family: Noto Serif JP, serif;
  font-size: 14px;
}

.cat {
  font-family: Marcellus, serif;
  font-size: 14px;
}

a {
  color: currentColor;
  cursor: crosshair;
  font-family: Noto Serif JP, serif;
  text-decoration: none;
}

a:hover {
  border-bottom: 1px solid;
  transition: all .5s;
}

.one .index, .two .index, .three .index, .four .index {
  font-family: Noto Serif JP, serif;
  font-size: 15px;
  font-weight: 600;
  position: absolute;
  top: 5px;
  left: -250px;
}

fieldset {
  border: 0;
}

legend {
  font-family: Cinzel, serif;
  font-size: 16px;
  font-weight: 700;
}

label {
  margin: 1.5rem 0 .5rem;
  font-family: Noto Serif JP, serif;
  display: block;
}

input {
  background-color: #0000;
  border: 0;
  border-bottom: 1px solid #fff;
  padding: .3rem 3.4rem;
  display: block;
}

input:focus {
  outline: 0;
}

input[placeholder] {
  color: #fff;
  padding: 0 7rem .5rem .5rem;
  font-family: Noto Serif JP, serif;
  font-size: 15px;
}

textarea {
  background-color: #0000;
  border: 0;
  border-bottom: 1px solid #fff;
  display: block;
}

textarea[placeholder] {
  color: #fff;
  padding: 0 6.5rem 0 .5rem;
  font-family: Noto Serif JP, serif;
  font-size: 15px;
}

textarea:focus {
  outline: 0;
}

.btn {
  letter-spacing: 6px;
  background-color: #ffffff84;
  border: 0;
  margin-top: 1rem;
  padding: .2rem 6rem;
  font-family: Cinzel, serif;
  font-size: 17px;
  font-weight: 600;
}

.btn:hover {
  background-color: #fff;
}

.footerCopyright {
  padding-bottom: 3.5rem;
  position: absolute;
  bottom: -600px;
  left: 430px;
}

.footerCopyright .en {
  letter-spacing: -.04rem;
  opacity: .5;
  font-family: Marcellus, serif;
  font-size: 60px;
  font-weight: 400;
  line-height: 1.1;
}

@media only screen and (max-width: 1180px) {
  .pageHeader {
    width: calc(var(--column) * 6);
  }

  .info_inner {
    right: 9rem;
  }

  h2 {
    font-size: 25px;
  }

  .cat {
    font-size: 13px;
  }

  .one .index, .two .index, .three .index, .four .index, .five .index {
    font-size: 15px;
  }

  .footerCopyright .en {
    font-size: 45px;
  }
}

