    font-size: 15px;
  }

  .footerCopyright {
    padding-bottom: 3.5rem;
    position: absolute;
    bottom: -600px;
    left: 150px;
  }

  .footerCopyright .en {
    font-size: 35px;
  }
}

@media only screen and (max-width: 764px) {
  .siteHeader_title {
    font-size: 16px;
