    font-size: var(--fs-heading);
  }

  .pageHeader .title .name {
    font-size: 45px;
  }

  .info_inner {
    right: 6rem;
  }

  h2 {
    font-size: 25px;
  }

  .cat {
    font-size: 12px;
  }

  .one .index, .two .index, .three .index, .four .index, .five .index {
    font-size: 14px;
  }

  .footerCopyright {
    padding-bottom: 3.5rem;
    position: absolute;
    bottom: -500px;
    left: 100px;
  }

  .footerCopyright .en {
    font-size: 25px;
  }
}

@media only screen and (max-width: 589px) {
  .pageHeader {
    height: calc(var(--fs-title)  + .01);
    width: calc(var(--column) * 3.2);
    padding-top: 90px;
  }

  .pageHeader .title .label {
    font-size: 20px;
  }

  .pageHeader .title .name {
    font-size: 40px;
  }

  .siteHeader_title {
    font-size: 14px;
    top: 40px;
    right: 20px;
  }

  .info_inner {
    right: 3rem;
  }

  h2 {
    font-size: 15px;
  }

  .cat {
    font-size: 6px;
  }

  .one .index, .two .index, .three .index, .four .index, .five .index {
    font-size: 12px;
    left: -150px;
  }

  legend {
    font-size: 15px;
  }

  textarea[placeholder] {
    padding: 0 3rem 0 .5rem;
  }

  input[placeholder] {
    padding: 0 3rem .5rem .5rem;
  }

  .btn {
    padding: .2rem 4.3rem;
    font-size: 15px;
  }

  .footerCopyright {
    padding-bottom: 3.5rem;
    position: absolute;
    bottom: -500px;
    left: 30px;
  }

  .footerCopyright .en {
    font-size: 25px;
  }
}

@media only screen and (max-width: 375px) {
  .pageHeader {
    height: calc(var(--fs-title)  + .01);
    width: calc(var(--column) * 3.2);
    padding-top: 80px;
  }

  .pageHeader .title .label {
    font-size: 14px;
  }

  .pageHeader .title .name {
    font-size: 30px;
  }

  .siteHeader_title {
    top: 70px;
    right: 20px;
  }

  .info_inner {
    right: 2rem;
  }

  h2 {
    font-size: 15px;
  }

  .cat {
    font-size: 4px;
  }

  .one .index, .two .index, .three .index, .four .index, .five .index {
    font-size: 12px;
    top: 2px;
    left: -95px;
  }

  .footerCopyright {
    padding-bottom: 3.5rem;
    position: absolute;
    bottom: -500px;
    left: 30px;
  }

  .footerCopyright .en {
    font-size: 25px;
  }
}

